@import "../../scss/variables";
@import "../../scss/mixins";

.headCountTable {
  table {
    max-height: 78vh;
    border-collapse: separate;
    td {
      display: block;
    }
    tr {
      &:has(.parent) {
        td {
          border-top-width: 2px;
        }
      }
      &:has(span.child > b) {
        td {
          border-top-width: 2px;
          &:first-of-type {
            border-left-width: 2px;
          }
          &:last-of-type {
            border-right-width: 2px;
          }
        }
      }
      &:has(span.child) {
        td {
          &:first-of-type {
            border-left-width: 2px;
          }
          &:last-of-type {
            border-right-width: 2px;
          }
        }
      }
      th,
      td {
        min-width: 100px;
        width: 100px;
        max-width: none;
        vertical-align: middle;
        &:nth-of-type(2) {
          display: none;
        }
        &:first-of-type {
          min-width: 200px;
          max-width: 300px;
          border-left: 1px solid #ccc;
        }
        &:nth-of-type(3) {
          min-width: 200px;
          max-width: 300px;
        }
      }

      th {
        & > * {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-bottom: -5px;
        }
      }
      td {
        &:has(.total) {
          background-color: #ffffe6;
        }
        &:has(.greenColumn) {
          background-color: #f2f8ef;
        }
        &:first-of-type {
          border-bottom: 0 !important;
        }
      }
    }
  }
}

